import { NextRequest, NextResponse } from "next/server";

export function middleware(req: NextRequest) {
  const sessionToken = req.cookies.get("session-us")?.value;

  // Check if the user is trying to access the cart
  if (req.nextUrl.pathname === "/cart" && !sessionToken) {
    const redirectUrl = new URL("/auth/signin", req.url);
    redirectUrl.searchParams.set("redirect", req.nextUrl.pathname); // Set the redirect path
    return NextResponse.redirect(redirectUrl);
  }

  return NextResponse.next();
}

// Middleware applies to these paths
export const config = {
  matcher: ["/cart"],
};
